<template>
  <div class="stockInfo" @click="closeByDialog">
    <AppHeader>
      <p slot="center">库存信息</p>
    </AppHeader>

    <div>
      <div class="filters">
        <div class="filters-label">选择日期:</div>
        <div class="date-filters">
          <input
            class="text-left"
            type="text"
            @click.stop="openByDialog"
            :value="calendar.display"
            readonly
          />
        </div>
        <transition name="fade">
          <div class="calendar-dialog" v-if="calendar.show">
            <div class="calendar-dialog-mask" @click="closeByDialog"></div>
            <div class="calendar-dialog-body">
              <calendar
                :range="calendar.range"
                :zero="calendar.zero"
                :lunar="calendar.lunar"
                :value="calendar.value"
                @select="calendar.select"
              ></calendar>
            </div>
          </div>
        </transition>
      </div>

      <div class="filters">
        <div class="filters-label">商品代码:</div>
        <div class="date-filters">
          <input class="text-left" type="text" v-model="code" placeholder="请输入搜索关键词" />
        </div>
      </div>

      <div class="filters">
        <div class="filters-label">第二代码:</div>
        <div class="date-filters">
          <input class="text-left" type="text" v-model="code2" placeholder="请输入搜索关键词" />
        </div>
      </div>

      <div class="filters">
        <div class="filters-label">商品名称:</div>
        <div class="date-filters">
          <input class="text-left" type="text" v-model="goodname" placeholder="请输入搜索关键词" />
        </div>
      </div>

      <div class="filters-tips">
        <p>提示：若要查询多个商品，可输入多个商品代码、第二代码，用英文逗号字符分隔","。如：10000010,10000011</p>
        <div class="filters-operation">
          <van-button class="inquiry" type="info" @click="beginInquiry">查询</van-button>
          <van-button class="clear" type="info" @click="clearFilter">清空</van-button>
        </div>
      </div>
    </div>

    <div class="detailed-table" v-if="hasData">
      <div class="table-content">
        <table>
          <thead>
            <tr>
              <th>商品代码</th>
              <th>商品名称</th>
              <th>第二代码</th>
              <th>小类代码</th>
              <th>库存数</th>
              <th>库存成本</th>
              <th v-if="isShow">最大库存数</th>
              <th v-if="isShow">最小库存数</th>
              <th v-if="isShow">建议</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(items, index) in currentData" :key="index">
              <td>{{ items.code }}</td>
              <td>{{ items.goodname }}</td>
              <td>{{ items.code2 }}</td>
              <td>{{ items.typeCode }}</td>
              <td>{{ items.inventoryNum }}</td>
              <td>{{ items.finvcost }}</td>
              <td v-if="isShow">{{ items.maxInvNum }}</td>
              <td v-if="isShow">{{ items.minInvNum }}</td>
              <td
                v-if="isShow"
                :class="items.inventoryNum > items.maxInvNum ? 'blue':items.minInvNum > items.inventoryNum ? 'red':''"
              >{{ items.inventoryNum > items.maxInvNum ? "退货":items.minInvNum > items.inventoryNum ? "补货":"正常"}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="pagination-module" v-if="hasData">
      <el-pagination
        background
        hide-on-single-page
        layout="prev, pager, next, jumper"
        :pager-count="5"
        :page-size="10"
        :total="recordcount"
        :current-page="currentPage"
        @current-change="pageCurrentChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import {
  Search,
  Cell,
  CellGroup,
  Icon,
  Field,
  DatetimePicker,
  Button,
  Popup
} from "vant";

import ElementUI from "element-ui";

Vue.use(Search)
  .use(Cell)
  .use(CellGroup)
  .use(Field)
  .use(DatetimePicker)
  .use(Popup)
  .use(Button)
  .use(Icon);
Vue.use(ElementUI);

// @ is an alias to /src
import AppHeader from "@/components/AppHeader.vue";
import "element-ui/lib/theme-chalk/index.css";
import calendar from "@/components/calendar/calendar.vue";

export default {
  name: "Search",
  components: {
    AppHeader,
    calendar
  },

  data() {
    return {
      calendar: {
        display: "2020/02/16", //输入框站位日期
        show: false, //是否显示
        // range: true,
        zero: true,
        value: [2019, 2, 16], //默认选中日期
        lunar: true, //显示农历
        select: value => {
          // console.log(value);
          this.calendar_startDate = value.join("-");
          this.calendar.show = false;
          this.calendar.value = value;
          this.calendar.display = value.join("-");
          if (this.formatDate.pastDate(0, "-") == value.join("-")) {
            this.isShow = true;
          } else {
            this.isShow = false;
          }
          this.$nextTick(() => {
            this.get_data();
          });
        }
      },
      calendar_startDate: "",
      calendar_endDate: "",
      code: "",
      code2: "",
      goodname: "",
      searchKey: "",

      hasData: false,
      resultData: [],
      currentPage: 1,
      recordcount: "",
      isShow: true
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.get_defaultData();
      this.get_data();
    });
  },
  methods: {
    get_defaultData() {
      this.getStoreInfo = this.$store.getters.getStoreInfo; //获取当前选择的门店信息
      // console.log(this.$store.state.defaultDate);
      const defaultTime = this.formatDate.pastDate(0, "-");
      const currentTime = this.formatDate.pastDate(0, "-");
      this.calendar_startDate = defaultTime; //日历默认开始时间
      this.calendar_endDate = currentTime; //日历默认结束时间
      this.calendar.value = defaultTime.split("-"); //日历默认选中日期
      //日历-输入框站位日期
      this.calendar.display = this.formatDate.pastDate(0, "-");
    },
    openByDialog() {
      this.calendar.show = true;
    },
    closeByDialog() {
      this.calendar.show = false;
    },
    pageCurrentChange(val) {
      this.currentPage = val;
      this.currentData = this.resultData.slice(
        this.currentPage * 10 - 10,
        this.currentPage * 10
      );
    },
    beginInquiry() {
      this.$nextTick(() => {
        this.get_data();
      });
    },
    clearFilter() {
      this.get_defaultData();
      this.code = "";
      this.code2 = "";
      this.goodname = "";
      this.searchKey = "";
      this.$nextTick(() => {
        this.get_data();
      });
    },
    // 获取数据
    get_data() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.getStoreInfo = this.$store.getters.getStoreInfo;
      let params = {
        storeId: this.getStoreInfo.gid,
        code: this.code,
        code2: this.code2,
        goodname: this.goodname,
        startDate: this.calendar_startDate,
        endDate: this.calendar_endDate,
        searchType: "T",
        searchKey: this.searchKey
      };
      this.serverHost
        .fetchPost("/sku/InvSkuByCondition", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          if (resultData.length == false) {
            this.hasData = false;
          } else {
            this.hasData = true;
            this.resultData = resultData;
            this.currentData = resultData.slice(
              this.currentPage * 10 - 10,
              this.currentPage * 10
            );
            this.recordcount = resultData.length;
          }
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    }
  }
};
</script>

